.product-card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  max-width: 200px;
  transition: transform .25s;
  display: flex;
  position: relative;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card__description {
  padding: 16px;
}

.product-card__icon {
  width: 160px;
  height: 134px;
  margin: 32px 19px 24px;
  display: flex;
}

.product-card__name {
  color: #060b35;
  text-align: left;
  height: fit-content;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card:hover .product-card__name {
  color: #34568b;
  transition: color .3s;
}

.product-card__code {
  color: #616070;
  text-align: left;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__rating {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  text-align: right;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__button {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 164px;
  height: 38px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.product-card__price-container {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-label {
  color: #616070;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  margin-right: 8px;
  list-style: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  justify-content: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header__navbar {
  justify-content: space-between;
  display: flex;
}

.header__nav-list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin: 0 0 0 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  color: #060b35;
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .2s;
  display: block;
  position: relative;
}

.header__nav-link:hover, .header__nav-link--active {
  color: #00acdc;
}

.header__nav-link--active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.is-active {
  margin: 0;
}

.catalog {
  padding: 50px 40px;
}

.catalog__grid {
  justify-items: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .catalog__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .catalog__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (width >= 1200px) {
  .catalog__grid {
    padding-inline: 88px;
  }
}

body, ul {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}
